<template>
  <div class="customer" id="customer">
    <div class="header-box">
      <img alt="" class="header-img" src="@/assets/images/customer/banner.png">
      <ul>
        <li :class="{active:current1}" @click="choose1">
          <h3>指示牌/导视牌设计</h3>
          <p>Sign Design</p>
        </li>
        <li :class="{active:current2}" @click="choose2">
          <h3>标识牌设计</h3>
          <p>Signboard Design</p>
        </li>
        <li :class="{active:current3}" @click="choose3">
          <h3>招牌设计</h3>
          <p>Signature</p>
        </li>
      </ul>
    </div>
    <sign v-if="current1"></sign>
    <signboard v-if="current2"></signboard>
    <signature v-if="current3"></signature>
    <app-footer></app-footer>
    <back-top target=".customer"></back-top>
  </div>
</template>

<script>
  import appFooter from '@/components/app-footer'
  import backTop from '@/components/back-top'
  import sign from '@/pages/customer/sign'
  import signboard from '@/pages/customer/signboard'
  import signature from '@/pages/customer/signature'

  export default {
    name: 'customer',
    components: {
      appFooter,
      backTop,
      sign,
      signboard,
      signature
    },
    data() {
      return {
        current1: true,
        current2: false,
        current3: false
      }
    },
    mounted() {
      this.$store.commit('setBackground', true)
    },
    methods: {
      choose1() {
        this.current1 = true
        this.current2 = false
        this.current3 = false
      },
      choose2() {
        this.current1 = false
        this.current2 = true
        this.current3 = false
      },
      choose3() {
        this.current1 = false
        this.current2 = false
        this.current3 = true
      }
    }
  }
</script>

<style lang="less">
  .customer {
    overflow: auto;
    height: 100%;

    .header-box {
      text-align: center;

      .header-img {
        width: 92%;
        margin-top: 80px;
      }

      ul {
        list-style: none;
        display: flex;
        flex-flow: row nowrap;
        margin: -9rem auto;
        margin-bottom: 3rem;
        width: 92%;
        z-index: 10;

        li {
          width: 22rem;
          height: 10rem;
          text-align: center;
          display: flex;
          flex-flow: column nowrap;
          justify-content: center;
          background: white;

          h3 {
            font-size: 1.6rem;
            color: rgba(20, 32, 94, 1);
            cursor: pointer;
          }

          p {
            font-size: 1.2rem;
            color: rgba(221, 221, 221, 1);
            cursor: pointer;
          }
        }

        .active {
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
          z-index: 15;

          h3, p {
            cursor: auto;
          }
        }
      }
    }
  }


</style>
